<template>
  <div>
    <qrcode-stream @decode="onDecode"></qrcode-stream>
    <br>
    <p>Número de invitados: {{countInvitados}}</p>
    <div>
      <b-modal
        id="modal"
        title="¡Bienvenido!"
        centered
        no-close-on-backdrop
        no-close-on-esc
        hide-header-close
      >
        <p class="my-4">{{ invitado.nombre }}</p>
        <p class="my-4">{{ invitado.mesa }}</p>
        <template #modal-footer="{ ok }">
          <b-button size="sm" variant="primary" @click="ok()">
            Aceptar
          </b-button>
        </template>
      </b-modal>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      invitado: {
        id: "",
        nombre: "",
        mesa: "",
      },
      invitados: [],
      identificadores: [],
      countInvitados: 0
    };
  },
  mounted() {
    this.contador();
  },
  methods: {
    async contador() {
      this.invitados = []
      this.identificadores = []
      await this.$firestore.list("invitados").then((items) => {
        this.invitados = items;
      });
      this.invitados.forEach(element => {
        if(this.identificadores.indexOf(element.data.id) == -1) {
          this.identificadores.push(element.data.id)
        }
      });
      this.countInvitados = this.identificadores.length
    },
    ok() {
      this.$bvModal.hide("modal");
    },
    onDecode(decodedString) {
      this.invitado.id = decodedString.substring(0, 4);
      this.invitado.nombre = decodedString.split(".")[0].substring(3);
      this.invitado.mesa = decodedString.split(".")[1];
      this.$bvModal.show("modal");
      this.$firestore.add("invitados", this.invitado);
      this.contador();
    },
  },
};
</script>